.header{
    display: flex;
    align-items: center;
    height: 60px;
    background-color: #131921;
    top: 0;
    position: sticky;
    z-index: 100;
}
.header_logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
    /* width: 100%; */
}

.header_searchicon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9040;
}

.header_searchinput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_line1{
    font-size: 10px;
}

.header_line2{
    font-size: 13px;
    font-weight: 800;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_option{
    color: white;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header_optionBasket{
    display: flex;
    align-items: center;
    color: white;
}

.header_basketCount{
    margin-left: 10px;
    margin-right: 10px;
}